const burgerBtn = document.querySelector('.main-nav__toggle');
const burgerCloseBtn = document.querySelector('.main-nav__close-button');
const navBlock = document.querySelector('.main-nav');
const navList = document.querySelector('.main-nav__list-menu');
const breakpointLg = window.matchMedia('(min-width:1024px)');

const openMenu = () => {
  burgerBtn.ariaPressed = 'true';
  navBlock.classList.add('main-nav--opened');
  window.disableBodyScroll(navList);
  burgerBtn.classList.add('no-pointer');
  setTimeout(() => {
    burgerBtn.classList.remove('no-pointer');
  }, 1000);
};

const closeMenu = () => {
  burgerBtn.ariaPressed = 'false';
  navBlock.classList.remove('main-nav--opened');
  window.enableBodyScroll(navList);
  burgerBtn.classList.add('no-pointer');
  setTimeout(() => {
    burgerBtn.classList.remove('no-pointer');
  }, 1200);
};

const closeMenuOnResize = () => {
  closeMenu();
};

const initToggle = () => {
  if (burgerBtn) {
    breakpointLg.addListener(closeMenuOnResize);
    burgerBtn.addEventListener('click', () => {
      if (burgerBtn.ariaPressed === 'true') {
        closeMenu();
      } else {
        openMenu();
      }
    });
  }

  if (burgerCloseBtn) {
    breakpointLg.addListener(closeMenuOnResize);
    burgerCloseBtn.addEventListener('click', () => {
      closeMenu();
    });
  }
};

export {initToggle};